    <div class="row" style="height: 95vh">
        <div class="container column scrolled" style="height: 90vh;">
            <p class="info">
                Interested in testing the beta versions, talking about languages, or worldbuilding? <br>
                Join <a rel="noreferrer" target="_blank" href="https://discord.gg/uDk2XDhh8K">Saturn's Sojourn</a>, 
                the home of the Lexicanter on <a rel="noreferrer" target="_blank" href="https://discord.gg/uDk2XDhh8K">Discord</a>!
            </p>
            <br>
            <p class="info">
                Support the continued developement of the app as a <a rel="noreferrer" target="_blank" href="https://patreon.com/saturnine_softworks">patron</a>,
            </p>
            <p class="info">
                or by buying me a <a rel="noreferrer" target="_blank" href="https://ko-fi.com/saturnine_softworks">coffee</a>!
            </p>

            <br><br>

            Below is a list of all changes made to the app since version 1.8, which is when the changelog was first added.<br>
            If you're looking for more information about how to use the app, check the Help tab or visit the 
            <a rel="noreferrer" target="_blank" href="https://github.com/Saturnine-Softworks/Lexicanter/wiki">Wiki</a>.<br> There is also
            an overview <a rel="noreferrer" target="_blank" href="https://www.youtube.com/watch?v=6_AwCC4XlvU&t=151s">tutorial video</a> for release 2.0.
            <br> If you're still having trouble, or need to contact the developer, visit us in the 
            <a rel="noreferrer" target="_blank" href="https://discord.gg/uDk2XDhh8K">Discord server</a>. 

            <br><hr/><br>

            <p><u>Patch 2.1.14</u></p>
            <p style="width: 70%; margin: auto; text-align: left; line-height: 1.6">
                 Fixed a bug with the orthography pattern replacement features which caused it to only replace the first instance of a pattern in each word.<br>
                 Added the ability to use <code>^</code> or <code>#</code> as word-end characters in the orthography pattern replacement fields. <br>
                 Fixed a reported bug with the Illegals field of the Advanced Phonotactics word generator which caused that field not to save its contents. <br>
            </p>
            <br>
            <p><u>Patch 2.1.13</u></p>
            <p style="width: 70%; margin: auto; text-align: left; line-height: 1.6">
                 Added an Illegals field to the Advanced Phonotactics word generator options. <br>
                 Added the Structures inputs to the Advanced Phonotactics word generator options and the corresponding syllable category syntax for this feature.<br>
                 (Finally) wrote the documentation on the word generator, which can be found in the Help tab. <br>
            </p>
            <br>
            <p><u>Patch 2.1.12</u></p>
            <p style="width: 70%; margin: auto; text-align: left; line-height: 1.6">
                 Added a new default text to the Pronunciations field in the Phonology tab to better guide beginners.<br>
                 Added an indicator on entries in the lexicon tab to show if their pronunciation was manually edited and thus not automatically updated by pronunciation rules.<br>
            </p>
            <br>
            <p><u>Patch 2.1.11</u></p>
            <p style="width: 70%; margin: auto; text-align: left; line-height: 1.6">
                 Added secondary location file saving, for those of you who want your lexicon files to save to another location on your computer every time you save. <br>
                 Added database syncing! Get your account credentials from the Lexicanter Discord bot, and then you can sync your files to the bot and between devices. <br>
                 A minor change to how app settings are saved. The old app settings files are now deprecated and will eventually no longer be supported. <br>
            </p>
            <br>
            <p><u>Patch 2.1.10</u></p>
            <p style="width: 70%; margin: auto; text-align: left; line-height: 1.6">
                 New tooltips have been added throughout the app. <br>
                 New All Hallow's Eve 2023 theme in the Holiday collection! <br>
            </p>
            <br>
            <p><u>Patch 2.1.9</u></p>
            <p style="width: 70%; margin: auto; text-align: left; line-height: 1.6">
                 Hot fix for a bug which caused data saved by the advanced word generator not to be loaded open re-opening a file. <br>
                 A new feature will be coming in 2.2 which requires a change to the way lexicon entries are formatted internally, which this update prepares for. <br>
            </p>
            <br>
            <p><u>New in 2.1</u></p>
            <p style="width: 70%; margin: auto; text-align: left; line-height: 1.6">
                 Introduced a plain text import feature for convenient clonging on the move. <br>
                 Added a reference window for loading secondary files in read-only mode. <br>
                 Support for custom fonts via new Orthographies feature. <br>
                 Under-the-hood file structure adjustments and read logic improvements. <br>
                 New phonological evolution tools! <br>
                 An alternative type of word generator for more advanced syllable structures. <br>
                 The option to save theme preferences on a file-by-file basis. <br>
                 New themes: <i>Magnolia</i> by Saturnine, <i>Crabapple</i> by Maarz, and <i>Eostre 2023</i> (a holiday theme released for beta users earlier this year). <br>
                 A number of bugs removed; probably some bugs added. Please report them if you find them! <br>
            </p>
            <br>
            <p><u>Patch 2.0.18</u></p>
            <p style="width: 70%; margin: auto; text-align: left; line-height: 1.6">
                 By request, added a new dropdown to Tag inputs which allows you to select from pre-existing tags in your lexicon. <br>
                 By request, added a new Help tab. It hosts the same information as the wiki, but is accessible offline. <br>
                 The Settings, Changelog, and Help tabs, as well as the window control buttons, now use Material Icons. 
            <br><br>
            </p>
            <p><u>Patch 2.0.17</u></p>
            <p style="width: 70%; margin: auto; text-align: left; line-height: 1.6">
                 Fixed an a reported bug with the inflections generation. <br>
                 Fixed an issue with tag searching. <br>
                 The font weight has been changed to an appropriate <i>Book</i> weight to improve readability on some systems and to many eyes.
            <br><br>
            </p>
            <p><u>Patch 2.0.16</u></p>
            <p style="width: 70%; margin: auto; text-align: left; line-height: 1.6">
                 Fixed a small bug with the new sound change engine. <br>
            <br><br>
            </p>
            <p><u>Patch 2.0.15</u></p>
            <p style="width: 70%; margin: auto; text-align: left; line-height: 1.6">
                 Fixed a reported bug with HTML export. <br>
                 Related to the above fix, technical limitations now prevent your theme from being exported with your HTML. Solutions are being investigated.
            <br><br>
            </p>
            <p><u>Patch 2.0.14</u></p>
            <p style="width: 70%; margin: auto; text-align: left; line-height: 1.6">
                 Fixed CSV export. <br>
                 Fixed a reported bug with HTML export. <br>
                 Fixed some reported and unreported issues with the sound change engine. <br>
                 There is now a text input designated for specifying categories for sound changes in an inflection group, to make everyone's life easier. <br>
                 Minor optimizations and performance improvements.
            <br><br>
            </p>
            <p><u>Patch 2.0.13</u></p>
            <p style="width: 70%; margin: auto; text-align: left; line-height: 1.6">
                 Linux support!
            <br><br>
            </p>
            <p><u>Patch 2.0.12</u></p>
            <p style="width: 70%; margin: auto; text-align: left; line-height: 1.6">
                 Fixed a reported bug which caused HTML export to fail. Expect expanded HTML export options in the future. <br>
                 Minor optimizations. 
            <br><br>
            </p>
            <p><u>Patch 2.0.11</u></p>
            <p style="width: 70%; margin: auto; text-align: left; line-height: 1.6">
                 Fixed a reported bug which caused a semi-rare soft-crash in certain cases when dealing with multiple lects. Again.
            <br><br>
            </p>
            <p><u>Patch 2.0.10</u></p>
            <p style="width: 70%; margin: auto; text-align: left; line-height: 1.6">
                 Fixed a reported bug which caused a semi-rare soft-crash in certain cases when dealing with multiple lects. <br>
                 Fixed a reported bug which caused CSV import to fail, and improved CSV import options. 
            <br><br>
            </p>
            <p><u>Patch 2.0.9</u></p>
            <p style="width: 70%; margin: auto; text-align: left; line-height: 1.6">
                 You can now write multple rules separated by a semicolon, which allows for multiple rules per table cell in the inflection tables. <br>
                 Fixed a reported bug which caused a soft crash when attempting to edit the last word in the lexicon if it had an inflections dropdown open.
            <br><br>
            </p>
            <p><u>New in 2.0</u></p>
            <p style="width: 70%; margin: auto; text-align: left; line-height: 1.6">
                 There is now a new sound change engine under the hood. Your old rules may no longer work; for assistance, you can contact the developer. <br>
                 Lexicon entries can now be separated into multiple <i>Senses</i>, each of which can have their own tags. <br>
                 There are new features accessible via new Advanced Settings. These include: <br>
                 New <i>Lect</i> features allow you to denote the ways your language may vary, particularly in semantics and pronunciation. <br>
                 New <i>Inflection</i> features, which include a new tab, which allows you to create inflectional paradigms for your language. <br>
                 New <i>Etymology</i> features, which include a new tab, allows you to create etymologies trees and view them in the lexicon. <br>
                 Check out the new <a rel="noreferrer" target="_blank" href="https://github.com/Saturnine-Softworks/Lexicanter/wiki">wiki page</a> 
                or <a rel="noreferrer" target="_blank" href="https://www.youtube.com/watch?v=6_AwCC4XlvU&t=151s">tutorial video</a> for more in-depth information! <br>
                 New app icons by Lyiusa! <br>
                 New themes: Juniper by Saturnine, and Midnight and Bone by <a rel="noreferrer" target="_blank" href="https://linktr.ee/maarzcreative">Maarz</a>!
            <br><br>
            </p>
            <p><u>Patch 1.11.4</u></p>
            <p style="width: 70%; margin: auto; text-align: left; line-height: 1.6">
                 Fixed a reported bug causing markdown not to work in variant descriptions of phrases.
            <br><br>
            </p>
            <p><u>Patch 1.11.3</u></p>
            <p style="width: 70%; margin: auto; text-align: left; line-height: 1.6">
                 Fixed a reported bug causing the alphabetizer pre-check to send false alerts when certain combining diacritics on certain characters were in the alphabet in certain orders.
            <br><br>
            </p>
            <p><u>Patch 1.11.2</u></p>
            <p style="width: 70%; margin: auto; text-align: left; line-height: 1.6">
                 The app now saves backup versions of your files in case things go wrong. <br>
                 Fixed a reported bug that caused the app to sometimes exit too quickly and not save when autosave was enabled.
            <br><br>
            </p>
            <p><u>Patch 1.11.1</u></p>
            <p style="width: 70%; margin: auto; text-align: left; line-height: 1.6">
                 Fixed a reported bug causing the Ignore Diacritics setting to be ignored during alphabet checks when adding words to the lexicon. <br>
            </p>
            <br>
            <p><u>New in 1.11</u></p>
            <p style="width: 70%; margin: auto; text-align: left; line-height: 1.6">
                 When you attempt to add a word to the lexicon, there is now an alert if the word contains characters (or polygraphs) not present in your alphabet. <br>
                 Fixed a reported bug causing external links in to not display correctly in the Lexicon tab specifically. <br>
                 Fixed a reported bug preventing the app from warning you that it will not save if there is no file name given. <br>
                 Fixed a minor bug with the Terminal theme when exported for HTML. <br>
            </p>
            <br>
            <p><u>New in 1.10</u></p>
            <p style="width: 70%; margin: auto; text-align: left; line-height: 1.6">
                 Added three new themes: Pomegranate, Wisteria, and Terminal. <br>
                 The word entry panel in the Lexicon tab is now collapsible. <br>
                 The Phrasebook now has active overwrite protection to prevent you from deleting your work by mistake. <br>
                 You can now search for an exact whole-word match in definitions and tags fields by using <code>!</code> as a prefix. <br>
                 For HTML exports, the appearance on mobile devices has been improved. <br>
                 Minor bug fixes for opening new windows from the File tab. <br>
                 Lots of uner-the-hood changes for the app's appearance in preparation for future features. <br>
            </p>
            <br>
            <p><u>Patch 1.9.5</u></p>
            <p style="width: 70%; margin: auto; text-align: left; line-height: 1.6">
                 Fixed a bug causing app-quit to be impossible sometimes. <br>
                 Fixed some minor bugs with the styles. <br>
                 Fixed a bug causing monospace toggle in the docs tab to be undoable. <br>
                 Fixed a bug causing external hyperlinks not to use the preferred browser, and is some cases not open at all. <br>
            </p>
            <br>
            <p><u>Patch 1.9.4</u></p>
            <p style="width: 70%; margin: auto; text-align: left; line-height: 1.6">
                 You can now hyperlink to entries in the lexicon. The link format is <code>lex::word</code>. <br>
                 The documentation tab would previously not adjust to the width of the window. That has been fixed. <br>
            </p>
            <br>
            <p><u>New in 1.9</u></p>
            <p style="width: 70%; margin: auto; text-align: left; line-height: 1.6">
                 Overhauled the Documentation tab, which now uses integrated <a href="https://editorjs.io/" 
                    target="_blank" rel="noreferrer">EditorJS</a> technology. <br> Markdown is no longer supported in this tab, 
                    in favor of the new WYSIWYG style with a toolbar visible when you highlight text. <br>
                 Note: The first time you load a file from an older version, there may be some formatting quirks. 
                    Most of these should sort themselves out after saving in the new version and re-loading. 
                    Please contact the developer if you run into persistent issues. <br>
                 Fixed a bug with the Open New Window button which caused it to fail to open new windows. <br>
                 The button to edit phrasebook entries has been change to right-click instead of left-click to
                    make it more difficult to accidentally overwrite work in progress, and to allow for
                    highlighting text. <br>
                 An HTML Docs-Only export option has been added. <br>
            </p>
            <br>
            <p><u>Patch 1.8.14</u></p>
            <p style="width: 70%; margin: auto; text-align: left; line-height: 1.6">
                 Fixed a few minor bugs with markdown parsing.<br>
                 Added monospace markdown with <code>``this``</code> syntax.<br>
                 Fixed a reported bug which affected the orthography testing area. <br>
            </p>
            <br>
            <p><u>New in 1.8</u></p>
            <p style="width: 70%; margin: auto; text-align: left; line-height: 1.6">
                 File storage has been migrated to make auto-save possible. <br>
                 Categories can now be defined and used in your Pronunciations rules. See the <a
                    target="_blank" href="https://lexicanter.com/docs" rel="noreferrer">docs page</a> for more info.<br>
                 Five new color themes: Light, Marine, Glade, Leatherbound, and Purple Maar (contributed by <a
                    target="_blank" href="https://linktr.ee/maarzcreative" rel="noreferrer">Maarz</a>). <br>
                 You can now load in your own custom CSS color themes.<br>
                 Definitions, descriptions, and documentation sections now support simple <a target="_blank"
                    href="https://lexicanter.com/docs#markdown" rel="noreferrer">markdown</a>. <br>
                 There's a new space in the Phonology tab to test your pronunciation rules. <br>
                 Tag searches no longer require an exact match. <br>
                 Several minor bug fixes, including one reported about tables being editable in the HTML
                export.<br>
            </p>
            <br>
        </div>
    </div>
</div>
